<template>
  <div>
    <img class="w-24 h-24 my-2" alt="logo" src="../assets/logo.png" />
    <img class="w-24 h-24 my-2" alt="logo" :src="'../assets/hangman/hangman-'+lettersRight.length+'.png'" />
    <p>{{userAnswer.join(' ')}}</p>
    <LetterInput :letters-right="lettersRight" :letters-wrong="lettersWrong" @inputLetter="handleLetter($event)"/>
  </div>
</template>

<script>
import axios from 'axios';
import LetterInput from '@/components/input/LetterInput';

export default {
name: "Hangman",
  components: { LetterInput },
  data() {
    return {
      countries: [],
      selectedCountry: [],
      lettersRight: [],
      lettersWrong: [],
      userAnswer: [],
      gameOver: false,
    };
  },
  methods: {
    pickCountry(){
      this.selectedCountry = this.countries[Math.floor(Math.random() * this.countries.length)].name.toLowerCase().split('');
      console.log(this.selectedCountry.join(''));
      this.userAnswer = this.selectedCountry.map(item => {
        return item === " " ? " " : "_";
      });
      console.log(this.userAnswer.join(''));
    },
    handleLetter(letter){
      if(this.selectedCountry.includes(letter)){
        this.lettersRight.push(letter);
        this.selectedCountry.map((item, index) => {
          if (item === letter){
            this.userAnswer[index] = item;
          }
        });

      }else{
        console.log('push '+letter);
        this.lettersWrong.push(letter);
      }
    }
  },
  async beforeCreate() {
    await axios
      .get('https://restcountries.eu/rest/v2/all?fields=name')
      .then(res => {
        this.countries = res.data;
      });
    this.pickCountry();
  },
}
</script>

